import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Observable} from 'rxjs';
import {Course, CoursesDataService, Semester} from '../courses-data.service';
import {map, startWith} from 'rxjs/operators';

@Component({
  selector: 'app-add-link',
  templateUrl: './add-link.component.html',
  styleUrls: ['./add-link.component.css']
})
export class AddLinkComponent implements OnInit {

  stateCtrl = new FormControl();
  filteredCourses: Observable<Course[]>;

  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;

  // tslint:disable-next-line:variable-name
  constructor(private _formBuilder: FormBuilder, private coursesDataService: CoursesDataService) {
    this.filteredCourses = this.stateCtrl.valueChanges
      .pipe(
        startWith(''),
        map(course => course ? this._filterStates(course) : [])
      );
  }

  private _filterStates(value: string): Course[] {
    return this.coursesDataService.queryCourses(value);
  }

  public getCurrentSemesters(): Semester[] {
    return this.coursesDataService.semesters;
  }


  ngOnInit(): void {
    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ['', Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', Validators.required]
    });
  }
}
