import {Injectable} from '@angular/core';

export interface Course {
  name: string;
  id: string;
}

export interface Semester {
  name: string;
  id: string;
}

export interface Link {
  name: string;
  link: string;
  imgUrl: string;
}

@Injectable({
  providedIn: 'root'
})

export class CoursesDataService {

  courses: Course[] = [
    {
      name: 'אלגברה לינארית 2',
      id: '20276',
    },
    {
      name: 'אלגברה לינארית 1',
      id: '20222',
    },
    {
      name: 'מעבדה בתכנות מערכות',
      id: '20465',
    },
    {
      name: 'מבוא למדעי המחשב ושפת Java',
      id: '20441',
    },
  ];
  semesters: Semester[] = [
    {name: 'ג2020', id: '2020-3'},
    {name: 'ב2020', id: '2020-2'},
    {name: 'א2020', id: '2020-1'}
  ];

  links: Link[] = [
    {
      name: 'אלגברה לינארית 1 2020ב',
      imgUrl: 'https://image.shutterstock.com/image-vector/algebra-concept-icon-algebraic-equations-600w-1426615184.jpg',
      link: 'https://whatsapp.com'
    },
    {
      name: 'אלגברה לינארית 1 2020ב',
      imgUrl: 'https://image.shutterstock.com/image-vector/algebra-concept-icon-algebraic-equations-600w-1426615184.jpg',
      link: 'https://whatsapp.com'
    },
    {
      name: 'אלגברה לינארית 1 2020ב',
      imgUrl: 'https://image.shutterstock.com/image-vector/algebra-concept-icon-algebraic-equations-600w-1426615184.jpg',
      link: 'https://whatsapp.com'
    },
  ];

  getSemesters(): Semester[] {
    return this.semesters;
  }

  getCourses(): Course[] {
    return this.courses;
  }

  queryCourses(query: string): Course[] {
    if (query.length <= 2) {
      return [];
    }
    return this.courses.filter(course => (course.name.toLowerCase().indexOf(query.toLowerCase()) >= 0) ||
      (course.id.toLowerCase().indexOf(query.toLowerCase()) >= 0));
  }

  queryLinks(courseId: string, semesterId: string): Link[] {
    return this.links;
  }

  constructor() {
  }
}
